@import url(https://fonts.googleapis.com/css?family=Rasa|Source+Code+Pro:400,800&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,400&display=swap);
@import url(https://stackpath.bootstrapcdn.com/font-awesome/4.7.0/css/font-awesome.min.css);
@import url(https://fonts.googleapis.com/css?family=Rasa|Source+Code+Pro&display=swap);
@import url(https://fonts.googleapis.com/css?family=Roboto:300,400&display=swap);
body {
  margin: 0;
}
.HomeScreen {
    background-color:#212121;
    color:#FFEA00;
    padding: 40px 5vw;
}

.row {
    margin-top: 0px;
    display:flex;
    font-family: 'Roboto';
    justify-content: flex-start;
}
#Name {
    font-size: 6vh;
    margin-bottom: 0;
    font-family: 'Roboto', sans-serif;
}
#TagLine {
    font-size: 30px;
    margin-top: 0;
    font-family: 'Roboto', sans-serif;
}

.AboutMe > p {
    /* text-align: justify; */
    margin-right: 10vw;
    max-width: 800px;
    font-size: 2vw;
    font-weight: 300;
    color:#FFEFEF;
}

.ContactMe {
    display:flex;
    justify-content: flex-end;
    margin-right: 10vw;
}
.ContactMe > a {
    margin-right:28px;
    color:#FFEA00;
}
.ContactMe > button {
    border:0;
    border-radius:12px;
    width:100px;
    font-size: 18px;
}
.myProfilePic {
    height:440px;
}
/* Media Queries */
@media screen and (max-width:1000px) {
    .row {
        flex-direction: column-reverse;
    }
}
@media screen and (max-width:500px) {
    .myProfilePic {
        height:350px;
    }
}
@media screen and (max-width:350px) {
    .myProfilePic {
        height:200px;
    }
}
@media screen and (max-width:750px) {
    .AboutMe > p {
        font-size:18px;
    }
}
@media screen and (min-width:400px) {
    .MyPic {
        margin-left: 5vw;
        margin-top: 150px;
    }
}
@media (max-width:395px) {
    .ContactMe {
        margin-right: 0;
        display: block;
    }
    .ContactMe > button {
        margin-top:10px;
}
}


@media (max-width:500px) {
    .ContactMe {
            margin-right: 0;
        
    }
    .fa {
        font-size: 25px !important;
    }
}
.title {
    text-decoration: underline;
    font-family: 'Rasa', serif;
    font-size: 22px;
    color:white;
}
.Project {
    font-family: 'Source Code Pro', monospace;
    font-size: 18px;
}
.ProjectWrapper {
    background-color:#212121;
    color:#FFEA00;
    padding: 10px 5vw;
    padding-bottom: 100px;
}
.ProjectWrapper > h3 {
    font-size: 50px;
    font-family: 'Roboto'
}
.myGrid {
    display: grid;
    grid-template-columns: 30% 30% auto;
    grid-gap: 100px;
    gap: 100px;
}
/* Media Queries */
@media screen and (max-width:700px) {
    .myGrid {
        grid-template-columns: auto;
        grid-gap: 40px;
    }
}
.workExp-wrapper {
    background-color:#212121;
    color:#FFEA00;
    padding: 10px 5vw;
    padding-bottom: 100px;
    font-family: 'Roboto';
    position: relative;
}
.workExp-wrapper > h1 {
    font-size: 6vh;

}
.wrap {
    margin-top: 100px;
}
.divider {
    width:20px;
    height:80%;
    position: absolute;
    background-color: #FFEA00;
    top:10%;
    left:50%;
    border-radius: 10px;
}
@media (max-width:1287px) {
    .divider {
        display: none;
    }
}
.card-wrapper {
    display: flex;
    width:100%;
}

.exp-card {
    font-family: 'Source Code Pro', monospace;
    border:1px solid #FFEA00;
    padding:20px;
    box-shadow: 17px 8px;
    display: inline-flexbox;
    flex-direction: column;
    max-width:500px;
    margin-bottom: 100px;
}
.exp-card > h4 {
    font-weight: 800;
}
.exp-card > h5 {
    padding-bottom: 5px;
    margin:0;
    border-bottom:4px  #FFEA00;
    border-bottom-style:  outset;
    display: inline-block;
}
.left {
    justify-content: flex-start;
}
.right {
    justify-content: flex-end;
}


.SkillCard {
    min-width: 200px;
    height: 300px;
    position: relative;
    overflow: hidden;
    padding: 20px;
    color: white;
    font-family: 'Roboto';
    margin-left: 80px;
    border-radius: 10px;
}

.SkillCard > h1 {
    text-align: end;
    font-size: 42px;

}

.SkillCard > img {
    max-width: 250px;
    min-width: 200px;
    position: absolute;
    bottom: -30px;
    left: -100px;
    
}
.skill-card-row {
    display: flex;
    overflow-x: hidden;
    width: 100%;
}

.skills {
    background-color:#212121;
    color:#FFEA00;
    padding-bottom: 10vh;
}
.head {
    padding: 0 5vw;
    font-size: 6vh;
    margin-bottom: 80px;
    margin-top: 0;
    font-family: 'Roboto', sans-serif;
}
.gallery-wrapper {
    background-color:#212121;
    color:#FFEA00;
    padding: 10px 5vw;
    padding-bottom: 100px;
    font-family: 'Roboto';
    position: relative;
}
.gallery-wrapper > h1 {
    margin-bottom: 5vh;
    font-size: 50px;
}

.imager_container {
    margin: auto;
    max-width: 70%;

}

.imager_container > img {
    max-width: 100%;
    margin-bottom: 20px;
}
